@use "../../sass/" as *; 
.banner 
{
    width: 100%;
    height:50vh;
    position: relative;
    background-position:center;
    background-size:cover;
    background-repeat:no-repeat;
    &::before 
    {
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        @include overlay();
    }
    &::after 
    {
        content:'';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100px;
        background-image:linear-gradient(to top, $body-bg, rgba($black,0));
    }
}
.movie-content
{
    @include flex(flex-start, flex-start);
    max-width:1260px;
    margin-left:auto;
    margin-right:auto;
    margin-top:-200px;
    position:relative;
    padding:0 2rem;
    &__poster
    {
        flex:1;
        @include mobile
        {
            display: none;
        }
        &__img
        {
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            border-radius:$border-radius;
            padding-top:165%;
        }

    }
    &__info
    {
        width:70%;
        padding-left:2rem;
        position: relative;
        @include mobile 
        {
            width: 100%;
            padding-left:0;
            margin-top:-8rem;
        }
        & > * 
        {
            margin-bottom:2rem;
        }
        .title 
        {
            font-size: 4rem;
            line-height: 1;
        }
        .genres
        {
            & > * ~ * 
            {
                margin-left:0.5rem;
            }
            &__item 
            {
                padding:0.5rem 1.5rem;
                border:2px solid $txt-color;
                border-radius: $border-radius;
                font-size: 0.8rem;
                font-weight: 600;
                background-color: $body-bg;
            }
        }
    }
}
.casts 
{
    &__item 
    {
        width:15%;
        @include mobile 
        {
            width:30%;
        }
        &__img 
        {
            padding-top:160px;
            margin-bottom:0.5rem;
            background-size: cover;        
            background-position: center;
            background-repeat: no-repeat;
            width:100%;
        }
        &__name 
        {
            font-size: 0.8rem;
        }
    }
}
.video 
{
    margin-bottom:3rem;
    iframe 
    {
        height:90vh;
        @include mobile 
        {
            height:auto;
        }
        @include tablet 
        {
            height: 70vh;
        }
    }
}